// src/calculators/HyOrderCalculator.jsx
import React, { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import '../styles/index.css';
import HyOrderForm from './HyOrderForm';
import HyOrderResult from './HyOrderResult';  // 결과 전용 컴포넌트 import
import { config } from '../config/config'; //고정 가격 가져오기기

export default function HyOrderCalculator() {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    paymentType: "postpaid",
    menu10: 0,
    menu15: 0,
    display10: 0,
    display15: 0,
    cardReader: 0,
    discountOption: "none",
    contractPeriod: "3year",
    serviceGen: "gen1"
  });
  const [prepaidAmount, setPrepaidAmount] = useState(0);
  const [includeVAT, setIncludeVAT] = useState(false);
  const [result, setResult] = useState(null);
  const [monthlyOnly, setMonthlyOnly] = useState(null);
  const [lumpsumOnly, setLumpsumOnly] = useState(null);
  const resultRef = useRef(null);

  // 선불형 선택 시 카드리더 기본 세팅
  useEffect(() => {
    if (formData.paymentType === "prepaid") {
      setFormData(prev => ({
        ...prev,
        cardReader: prev.menu10 + prev.menu15
      }));
    }
  }, [formData.paymentType, formData.menu10, formData.menu15]);

  const handleChange = e => {
    const { name, value } = e.target;
    const numFields = ["menu10","menu15","display10","display15","cardReader"];
    setFormData(prev => ({
      ...prev,
      [name]: numFields.includes(name) ? Number(value) : value
    }));
  };

  const handleCalculate = () => {
    const c = formData.contractPeriod;
    const m10 = formData.menu10 * config[`price_menu10_${c}`];
    const m15 = formData.menu15 * config[`price_menu15_${c}`];
    const d10 = formData.display10 * config[`price_display10_${c}`];
    const d15 = formData.display15 * config[`price_display15_${c}`];
    const card = formData.paymentType === "prepaid"
      ? formData.cardReader * config.price_cardreader
      : 0;
    const totalTerms = formData.menu10 + formData.menu15 + formData.display10 + formData.display15;
    const service = config[`price_service_${formData.serviceGen}`] * totalTerms;

    let tot = 0;
    let mon = null;
    let lump = null;

    if (c === "lumpsum") {
      lump = m10 + m15 + d10 + d15;
      mon = card + service;
      if (includeVAT) {
        lump = Math.round(lump * 1.1);
        mon = Math.round(mon * 1.1);
      }
      setLumpsumOnly(lump);
      setMonthlyOnly(mon);
      tot = lump + mon;
    } else {
      tot = m10 + m15 + d10 + d15 + card + service - prepaidAmount;
      if (includeVAT) tot = Math.round(tot * 1.1);
      setLumpsumOnly(null);
      setMonthlyOnly(null);
    }

    setResult(tot);
    // 결과로 스크롤
    setTimeout(() => {
      resultRef.current?.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }, 100);
  };

  const goHome = () => navigate('/');

  return (
    <div className="max-w-xl mx-auto p-4 bg-white shadow rounded-xl">
      <h2 className="text-xl font-bold mb-4 text-center">KT 하이오더 계산기</h2>

      {/* 입력 폼 */}
      <HyOrderForm
        formData={formData}
        handleChange={handleChange}
        prepaidAmount={prepaidAmount}
        setPrepaidAmount={setPrepaidAmount}
        includeVAT={includeVAT}
        setIncludeVAT={setIncludeVAT}
        handleCalculate={handleCalculate}
      />

      {/* 결과 표시를 전용 컴포넌트로 분리 */}
      <div ref={resultRef} className="mt-4">
        <HyOrderResult
          result={{ total: result, monthly: monthlyOnly, lumpsum: lumpsumOnly }}
        />
      </div>

      {/* 메인으로 돌아가기 */}
      <div className="mt-6 text-center">
        <button
          onClick={goHome}
          className="px-4 py-2 bg-gray-200 rounded hover:bg-gray-300"
        >
          메인으로 돌아가기
        </button>
      </div>
    </div>
  );
}
