// src/calculators/HyOrderForm.jsx
import React from 'react';
import { config } from '../config/config';

export default function HyOrderForm({
  formData,
  handleChange,
  prepaidAmount,
  setPrepaidAmount,
  includeVAT,
  setIncludeVAT,
  handleCalculate
}) {
  // 약정 기간별 단가
  const menu10Label = config[`price_menu10_${formData.contractPeriod}`] || 0;
  const menu15Label = config[`price_menu15_${formData.contractPeriod}`] || 0;
  const display10Label = config[`price_display10_${formData.contractPeriod}`] || 0;
  const display15Label = config[`price_display15_${formData.contractPeriod}`] || 0;
  // 서비스 세대별 단가
  const service1Price = config.price_service_gen1;
  const service2Price = config.price_service_gen2;

  return (
    <div className="grid gap-4">
      {/* 하이오더 서비스 세대 */}
      <div>
        <label className="block text-sm font-medium">하이오더 서비스 세대</label>
        <select
          name="serviceGen"
          className="w-full border rounded p-2"
          value={formData.serviceGen}
          onChange={handleChange}
        >
          <option value="gen1">1세대 (단말당 {service1Price.toLocaleString()}원)</option>
          <option value="gen2">2세대 (단말당 {service2Price.toLocaleString()}원)</option>
        </select>
      </div>

      {/* 결제 방법 */}
      <div>
        <label className="block text-sm font-medium">결제 방법</label>
        <select
          name="paymentType"
          className="w-full border rounded p-2"
          value={formData.paymentType}
          onChange={handleChange}
        >
          <option value="postpaid">후불형</option>
          <option value="prepaid">선불형</option>
        </select>
      </div>

      {/* 메뉴판 수량 입력 */}
      <div>
        <label className="block text-sm">메뉴판 10인치 ({menu10Label.toLocaleString()}원)</label>
        <input
          type="number"
          name="menu10"
          className="w-full border rounded p-2"
          min={0}
          value={formData.menu10}
          onChange={handleChange}
        />
      </div>
      <div>
        <label className="block text-sm">메뉴판 15인치 ({menu15Label.toLocaleString()}원)</label>
        <input
          type="number"
          name="menu15"
          className="w-full border rounded p-2"
          min={0}
          value={formData.menu15}
          onChange={handleChange}
        />
      </div>

      {/* 알림판 수량 입력 */}
      <div>
        <label className="block text-sm">알림판 10인치 ({display10Label.toLocaleString()}원)</label>
        <input
          type="number"
          name="display10"
          className="w-full border rounded p-2"
          min={0}
          value={formData.display10}
          onChange={handleChange}
        />
      </div>
      <div>
        <label className="block text-sm">알림판 15인치 ({display15Label.toLocaleString()}원)</label>
        <input
          type="number"
          name="display15"
          className="w-full border rounded p-2"
          min={0}
          value={formData.display15}
          onChange={handleChange}
        />
      </div>

      {/* 카드리더 수량 (선불형) */}
      {formData.paymentType === 'prepaid' && (
        <div>
          <label className="block text-sm">카드리더 수량</label>
          <input
            type="number"
            name="cardReader"
            className="w-full border rounded p-2"
            min={0}
            value={formData.cardReader}
            onChange={handleChange}
          />
        </div>
      )}

      {/* 약정기간 */}
      <div>
        <label className="block text-sm font-medium">약정기간</label>
        <select
          name="contractPeriod"
          className="w-full border rounded p-2"
          value={formData.contractPeriod}
          onChange={handleChange}
        >
          <option value="3year">3년</option>
          <option value="2year">2년</option>
          <option value="lumpsum">일시불</option>
        </select>
      </div>

      {/* 선납 입금 */}
      <div>
        <label className="block text-sm font-medium">선납 입금</label>
        <div className="flex items-center gap-2">
          <input
            type="number"
            name="prepaidAmount"
            className="w-1/2 border rounded p-2"
            value={prepaidAmount}
            onChange={e => setPrepaidAmount(Number(e.target.value) || 0)}
          />
          <span className="text-sm text-gray-600">원</span>
        </div>
      </div>

      {/* 할인 옵션 */}
      <div>
        <label className="block text-sm font-medium">할인</label>
        <select
          name="discountOption"
          className="w-full border rounded p-2"
          value={formData.discountOption}
          onChange={handleChange}
        >
          <option value="none">없음</option>
          <option value="ktfamily">KT 결합할인</option>
          <option value="longterm">장기고객 할인</option>
        </select>
      </div>

      {/* 부가세 포함 여부 */}
      <div>
        <label className="block text-sm font-medium">부가세 포함 여부</label>
        <select
          className="w-full border rounded p-2"
          value={includeVAT ? 'include' : 'exclude'}
          onChange={e => setIncludeVAT(e.target.value === 'include')}
        >
          <option value="exclude">미포함</option>
          <option value="include">포함</option>
        </select>
      </div>

      {/* 계산하기 버튼 */}
      <button
        onClick={handleCalculate}
        className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700"
      >
        계산하기
      </button>
    </div>
  );
}